<template>
<div class="user-card">
  <img :src="userAvatarUrl" class="user-card__item user-card__img"/>
  <div class="user-card__item user-card__content">
    <p class="content__item user-card__username">{{ userName }}</p>
    <p class="content__item user-card__employment">{{ userEmployment }}</p>
    <p class="content__item user-card__age">{{ viewUserAge(userAge) }}</p>
  </div>

</div>
</template>

<script>
import axios from 'axios';
import moment from 'moment';
export default {
  data() {
    return {
      user: [],
      dateOfBirth: '',
      userAge: '',
      userEmployment: '',
      userAvatarUrl: '',
      userName: '',
    }
  },
  methods: {
    viewUserAge(age) {
      let userAgeString;
      let count = age % 100;
      if (count >= 5 && count <= 20) {
        userAgeString = `${age} лет`;
      } else {
        count = count % 10;
        if (count == 1) {
          userAgeString = `${age} год`;
        } else if (count >= 2 && count <= 4) {
          userAgeString = `${age} года`;
        } else {
          userAgeString = `${age} лет`;
        }
      }
      return userAgeString;
    },
    async getUser() {
      try {
        const response = await axios.get('https://random-data-api.com/api/users/random_user');
        this.user = response.data;
        this.userName = this.user.first_name;
        this.dateOfBirth = this.user.date_of_birth;
        this.userEmployment = this.user.employment.title;
        this.userAvatarUrl = this.user.avatar;
        this.userAge = moment().diff(this.dateOfBirth, 'years', false);

        // console.log(response.data);
      } catch (error) {
        console.log(error)
      }
    }
  },
  mounted() {
    this.getUser();
  }
}
</script>

<style scoped lang="scss">
.user-card {
  display: flex;
  background: white;
  padding: 2em;
  border-radius: 20px;
  .user-card__img {
    width: 5em;
    height: 5em;
    margin-right: 1em;
  }
  .user-card__content {

  }

}

</style>