<template>
  <div class="beer-card">
    <button type="button" @click="getRandomBeer">Подобрать пиво</button>

    <div>
      <table v-if="tableShow" >
        <caption>
          Рекомендую вам пиво {{ beer.name }}
        </caption>
        <tr v-for="(value, key) in beer">
          <td>
            {{ key }}
          </td>
          <td>
            {{ value }}
          </td>
        </tr>
      </table>
      <div v-else>
        Нажмите на кнопку и я вам скажу какое пиво лучше выбрать
      </div>
    </div>
  </div>

</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      beer: [],
      tableShow: false,
    }
  },
  methods: {
    async getRandomBeer() {
      try {
        const response = await axios.get('https://random-data-api.com/api/beer/random_beer');
        this.beer = response.data;
        console.log(response.data);
        this.tableShow = true;
      } catch (error) {
        console.log(error)
      }
    }
  },
}
</script>

